#wp-admin-bar-my-sites > .ab-sub-wrapper {
  // display: grid!important;
  grid-template-columns: min-content;
  height: 100vh;
  position: absolute;
}

#wp-admin-bar-my-sites.hover > .ab-sub-wrapper {
  display: grid!important;
}

#site-list{
  overflow-y: scroll;
  #sites{
    padding-bottom: 5em;
    .site{
      padding: 0 10px;
      border-radius: 15px;
      margin: 15px;
      border: 1px solid rgba(0,0,0,.50);
      background-color: rgba(0,0,0,.20);
      display: grid;
      grid-template-columns: 32px repeat(5, max-content);
      grid-template-rows: 1fr 1fr;
      &:hover{
        border: 1px solid rgba(0,0,0,.25);
        background-color: rgba(0,0,0,.1);
      }
      #msl-favicon{
        grid-column: 1/3;
        grid-row: 1/3;
        width: 32px;
        height: auto;
        justify-self: center;
        align-self: center;
      }
      #msl-sitename{
        grid-column: 3/8;
        grid-row: 1/2;
        font-weight: bold;
      }
    }
  }
}

#wp-admin-bar-my-sites .ab-sub-wrapper ul li .ab-sub-wrapper {
  display: inherit;
  position: relative;
  height: auto;
}

#wp-admin-bar-my-sites .ab-sub-wrapper ul li .ab-sub-wrapper ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

#network-admin{
  background-color: darkslateblue;
  padding:10px;
    link{
      font-weight: bold;
    }
    ul{
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr !important;
      display: grid;
    }
    #search{
      width: 100%;
      border: none;
      border-radius: 5px;
      margin-top: 5px;
    }
}



ul#wp-admin-bar-my-sites-list .menupop .ab-sub-wrapper .ab-submenu li {
  display: flex;
  position: relative;
  margin: 5px 0 5px;
}

.menupop li#wp-admin-bar-network-admin.hover > .ab-sub-wrapper,
.menupop li#wp-admin-bar-network-admin:hover > .ab-sub-wrapper {
  margin-left: 0 !important;
  margin-top: 0 !important;
}

.ab-sub-secondary li.hover .ab-sub-wrapper,
.ab-sub-secondary li:hover .ab-sub-wrapper {
  margin-left: 0 !important;
  margin-top: 0 !important;
}

#wpadminbar #wp-admin-bar-my-sites a.ab-item,
#wpadminbar #wp-admin-bar-site-name a.ab-item {
  min-width: inherit;
}

#wpadminbar #wp-admin-bar-my-sites a.ab-item span.wp-admin-bar-arrow:before,
#wpadminbar #wp-admin-bar-site-name a.ab-item span.wp-admin-bar-arrow:before {
  display: none;
}

#search div {
  padding: 5px;
}

input#sites-search {
  width: 100%;
  text-indent: 10px;
}


#sorter{
  height: 100vh;
    background-color: darkslateblue;
    ul{
      display: grid;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      height: 100vh;
      li{
        transform: rotateZ(180deg);
        display: flex;
        align-items: center;
        justify-content: center;
        white-space: nowrap;
        text-align: center;
        margin: 0;
        padding: 0;
        width: 50px;
        height: 100%;
        writing-mode: vertical-lr;
        text-orientation: sideways-right;
      }
    }
}
